<template>
  <!-- 我的收藏  视频 -->
  <div class="videolist">
    <div class="videolist__head">
      <div class="videolist__head__title">收藏的视频 <span class="videolist__head__title-count">({{ count }})</span> </div>
      <div class="videolist__head__search"></div>
    </div>
    <div class="videolist__container">
      <VCoverVue v-for="item in videoList"
      :key="item.vid"
      :item="item"></VCoverVue>
    </div>
  </div>
</template>

<script>
import VCoverVue from './VCover.vue'
import { getMV } from '../../api/api_mycollect'

export default {
  name: 'VideoList',
  components: { VCoverVue },
  data () {
    return {
      videoList: null,
      mvList: null,
      count: null
    }
  },
  methods: {
    async getData () {
      const res = await getMV()
      this.videoList = res.data.data
      this.count = res.data.count
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="scss" scoped>
.videolist{
  &__head{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
    &__title{
      font-size: 14px;
      font-weight: 500;
      &-count{
        font-size: 14px;
        color: #e1e1e1;
      }
    }
  }
  &__container{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid: 20px;
}
  }
</style>
